.staside {
  background-color: #fafafa;
  min-height: 100vh;
}

.staside-body {
  width: 100%;
}

.staside-body__top {
  width: 100%;
  height: 48px;
}

.staside-body__content {
  width: 100%;
  padding: 30px;
  border-bottom: 1px solid #d1d1d1;
}

.staside-body__content:last-child {
  border: none;
}

.staside-body__content h1 {
  color: #7f7ba0;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 0.4px;
  margin-bottom: 40px;
}

.staside-navbar {
  width: 100%;
}

.staside-navbar a {
  width: 100%;
  display: flex;
  align-items: center;
  text-decoration: none;
  margin-bottom: 20px;
}

.staside-navbar__icon {
  width: 31px;
  height: 31px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.staside-navbar a img {
  width: 100%;
  height: 100%;
  border-radius: 4px;
  object-fit: contain;
}
.staside-navbar a span {
  color: #7f7ba0;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.3px;
  margin-left: 16px;
}

.staside-navbar a svg {
  width: 22px;
  height: 22px;
  fill: #7f7ba0;
}

.staside-navbar__icon p {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  background-color: #c6c6c6;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
}

.staside-coming {
  border-radius: 2px;
  background-color: #c1eeff;
  padding: 3px 5px;
  color: #00b9ff;
  font-size: 8px;
  font-weight: 400;
  margin-left: auto;
}

.staside-add {
  width: 100%;
}

.staside-add button {
  color: #00b9ff;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.3px;
  border: none;
  background-color: transparent;
  padding: 0;
  margin-top: 30px;
}

.staside-add a {
  color: #00b9ff;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.3px;
  border: none;
  background-color: transparent;
  padding: 0;
  margin-top: 30px;
  text-decoration: none;
  display: block;
}
