.stlayout {
  background-color: #ffffff;
  width: 100%;
}

.stlayout-body {
  width: 100%;
  display: grid;
  grid-template-columns: 280px 1fr;
}

.settings {
  width: 100%;
}

.settings-body {
  width: 100%;
}
.settings-head {
  width: 100%;
  border-bottom: 1px solid #fff5f5;
  padding: 8px;
}

.settings-head h2 {
  color: #100e39;
  font-size: 30px;
  font-weight: 700;
  letter-spacing: 0.6px;
  text-align: center;
}

.settings-content {
  width: 100%;
  padding-top: 40px;
  padding-left: 125px;
  padding-right: 125px;
}
.settings-content__head {
  width: 100%;
  margin-bottom: 40px;
}
.settings-content__head h3 {
  color: #100e39;
  font-size: 30px;
  font-weight: 700;
  letter-spacing: 0.6px;
}
