.folder {
  width: 100%;
}

.folder-body {
  width: 100%;
}
.folder-container {
  width: 100%;
  max-width: 735px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 75px;
  row-gap: 75px;
}

.folder-content {
  box-shadow: 0 6px 3px rgba(0, 0, 0, 0.02);
  border-radius: 8px;
  background-color: #ffffff;
  padding: 32px;
  padding-right: 16px;
  display: flex;
  flex-direction: column;
}

.folder-content:hover {
  border-radius: 21px;
  box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.08);
  background-color: #ffffff;
}

.folder-content__head {
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: auto;
}

.folder-content__head h2 {
  color: #555464;
  font-size: 23px;
  font-weight: 500;
}

.folder-content__head span {
  margin-left: 20px;
}

.folder-content__head button {
  /* padding: 0; */
  background-color: #ffffff;
  border: none;
  margin-left: auto;
}

.folder-content__head button svg {
  width: 4px;
  height: 13px;
  fill: #555464;
}

.folder-tools {
  width: min-content;
  white-space: nowrap;
  position: absolute;
  top: -45px;
  left: 108%;
  z-index: 100;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08);
  background-color: #ffffff;
}

/* .folder-tools */
.folder-tools__body {
  width: 100%;
  height: 100%;
  position: relative;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08);
  background-color: #ffffff;
}
.folder-tools__body::before {
  content: "";
  width: 20px;
  height: 20px;
  /* box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08); */
  background-color: #ffffff;
  display: block;
  position: absolute;
  left: -10px;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
}

.folder-tools__content {
  padding: 16px;
  padding-right: 35px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
}

.folder-tools__content p {
  color: #656565;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 0.2px;
  margin-bottom: 16px;
}

.folder-tools__content p:last-child {
  margin-bottom: 0;
}
.folder-tools__content p a {
  color: #656565;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 0.2px;
  text-decoration: none;
}

.folder-content__center {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: auto;
  margin-bottom: auto;
  height: 0;
  overflow: hidden;
  transition: all 0.3s;
}
.folder-content__center.show {
  height: auto;
}

.folder-content__tools {
  display: flex;
  align-items: center;
}

.folder-content__tools span {
  border-radius: 2px;
  background-color: #eeeeee;
  color: #555464;
  font-size: 20px;
  font-weight: 400;
}

.folder-content__tools div {
  color: #eeeeee;
  margin-left: 12px;
  margin-right: 12px;
}

.folder-content__tools img {
  width: 24px;
  height: 24px;
  object-fit: contain;
}

.folder-content__center button {
  padding: 0;
  background-color: transparent;
  border: none;
}

.folder-content__center button svg {
  fill: #555464;
}

.folder-content__bottom {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: auto;
  position: relative;
}

.folder-content__images {
  display: flex;
  align-items: center;
}

.folder-content__images img {
  width: 46px;
  height: 46px;
  border: 1px solid #ffffff;
  border-radius: 50%;
  position: relative;
  left: 0;
}
.folder-content__images img:nth-child(2) {
  left: -25px;
}
.folder-content__images img:nth-child(3) {
  left: -50px;
}

.folder-content__more {
  width: 46px;
  height: 46px;
  border: 1px dashed #d9d9d9;
  background-color: #c1eeff;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;
  left: -70px;
}
.folder-content__more span {
  color: #00b9ff;
  font-size: 9px;
  font-weight: 500;
  display: block;
  text-align: center;
}

.folder-content__plus {
  width: 34px;
  height: 34px;
  border: 1px dashed #00b9ff;
  background-color: #c1eeff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.folder-content__plus svg {
  width: 12px;
  height: 12px;
  fill: #00b9ff;
}

.folder-persons {
  width: 100%;
  position: relative;
}
.folder-persons__hide {
  border-radius: 2px;
  background-color: #c1eeff;
  display: inline-block;
  padding: 6px;
  line-height: 0;
  position: absolute;
  top: 0px;
  right: 6px;
  cursor: pointer;
}
.folder-persons__hide svg {
  width: 7.7px;
  height: 5.2px;
  fill: #00b9ff;
}
.folder-persons__body {
  width: 100%;
}

.folder-persons__head {
  display: flex;
  border-radius: 5px;
}

.folder-persons__head input {
  border: none;
  background-color: transparent;
  height: 30px;
  width: min-content;
  flex-grow: 1;
}

.folder-persons__head button {
  border: none;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}

.folder-persons__head button svg {
  fill: #dddddd;
}

.folder-persons__list {
  width: 100%;
  margin-top: 30px;
}
.folder-persons__list::-webkit-scrollbar {
  display: none;
}

.folder-persons__item {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.folder-persons__item img {
  width: 34px;
  height: 34px;
  border: 1px solid #707070;
  border-radius: 50%;
}

.folder-persons__item span {
  width: min-content;
  flex-grow: 1;
  color: #152243;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.14px;
  line-height: 18px;
  padding-left: 20px;
  padding-right: 10px;
}

.folder-persons__item button {
  border-radius: 5px;
  border: 1px solid #00b9ff;
  background-color: #ffffff;
  padding: 3px 12px;
  color: #00b9ff;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.14px;
  line-height: 18px;
}

.folder-modal {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.folder-modal__body {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.13);
  display: flex;
}

.folder-modal__content {
  width: 100%;
  max-width: 560px;
  border-radius: 12px;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  background-color: #ffffff;
  height: min-content;
  margin: auto;
  padding: 60px;
  padding-top: 16px;
  position: relative;
}

.folder-modal__content .crmeeting-form__group label {
  padding-left: 0;
}

.folder-modal__head {
  width: 100%;
  margin-bottom: 50px;
}
.folder-modal__head h2 {
  font-size: 22px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.18;
  letter-spacing: normal;
  text-align: center;
  color: #253655;
}

.folder-modal__close {
  padding: 0;
  background-color: transparent;
  border: none;
  position: absolute;
  right: 10px;
  top: 10px;
}

.folder-modal__close svg {
  fill: #ff3286;
}


.milstone {
  width: 100%;
}

.milstone-body {
  width: 100%;
}

.milstone-body__head {
  width: 100%;
  margin-bottom: 35px;
}
.milstone-body__head h2 {
  font-size: 30px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
}

.milstone-inner {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 32px 32px 32px 32px ;
  column-gap: 20px;
  margin-bottom: 20px;
  align-items: center;
}

.milstone-inner__item {
  display: flex;
  align-items: center;
}

.milstone-inner__item span {
  font-size: 15px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: center;
  color: #042141;
  padding-left: 5px;
}

.milstone-inner__doc img {
  max-width: 20px;
  object-fit: contain;
}

.milstone-inner__doc a {
  font-size: 15px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: center;
  color: #bab9bb;
  padding-left: 20px;
  text-decoration: none;

}

.milstone-inner__user img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: solid 1px #707070;
  object-fit: cover;
}
.milstone-inner__user span {
  padding-left: 20px;
}

.milstone-inner__status svg {
  width: 9px;
  height: 9px;
}

.milstone-inner__status.active {
fill: #6ac000;
}

.milstone-inner__tools button {
background-color: transparent;
border: none;
padding: 0;
}

.milstone-inner__tools button svg {
  width: 20px;
  height: 20px;
  fill: #6f6e7c;;
}