.settings-profile {
  width: 100%;
  border-bottom: 1px solid #d1d1d1;
  padding-bottom: 16px;
}

.settings-profile__picture {
  width: 100%;
  display: flex;
}

.settings-profile__image {
  width: 90px;
  height: 90px;
  border-radius: 5px;
  cursor: pointer;
  flex-grow: 0;
  position: relative;
}
.settings-profile__image img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
}

.settings-profile__image > p{
    width: 100%;
    height: 100%;
    display: block;
    text-align: center;
    font-size: 27px;
    background: #01b9ff;
    border-radius: 50%;
    padding-top: 2rem;
    color: white;
}

.settings-change__photo {
  position: absolute;
  bottom: 5px;
  right: 0px;
  width: 21px;
  height: 21px;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.08);
  background-color: #ffffff;
  border-radius: 50%;
  display: flex;
}
.settings-change__photo svg {
  width: 10px;
  height: 10px;
  fill: #00b9ff;
  margin: auto;
}

.settings-profile__name {
  width: min-content;
  flex-grow: 1;
  padding-left: 16px;
}
.settings-profile__name h3 {
  color: #100e39;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 0.4px;
  line-height: 15px;
  margin-bottom: 20px;
}

.settings-profile__name span {
  color: #7f7ba0;
  font-size: 20px;
  font-weight: 400;
  letter-spacing: 0.4px;
  line-height: 15px;
}

.settings-profile__details {
  width: 100%;
  margin-top: 20px;
}

.settings-profile__details-content {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.settings-profile__details-content svg {
  width: 13px;
  height: 13px;
  fill: #7f7ba0;
  margin-right: 10px;
}
.settings-profile__details-content span {
  color: #7f7ba0;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.3px;
}

.settings-form {
  width: 100%;
  margin-top: 35px;
  /* border-bottom: 1px solid #d1d1d1; */
  padding-bottom: 40px;
}
.settings-form h3 {
  color: #100e39;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: 0.44px;
}

.settings-form form {
  width: 100%;
  max-width: 500px;
  margin-top: 40px;
}

.settings-form__group {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}
.settings-form__group label {
  width: 100%;
