.preferences-choose {
  width: 100%;
  margin-top: 45px;

  padding-bottom: 10px;
}

.preferences-choose__head {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 30px;
}

.preferences-choose__info {
  display: flex;
  align-items: center;
}

.preferences-choose__info {
  color: #100e39;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.36px;
}

.preferences-choose__info.title {
  color: #100e39;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.5px;
}

.preferences-choose__info.title span {
  margin-left: 20px;
}

.preferences-checkbox {
  width: 40px;
  display: block;
  flex-shrink: 0;
}

.preferences-checkbox span {
  width: 100%;
  height: 20px;
  border-radius: 10px;
  background-color: #c6c6c6;
  display: block;
  position: relative;
  cursor: pointer;
}

.preferences-checkbox span::before {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #fafafa;
  display: block;
  position: absolute;
  top: 2px;
  left: 3px;
  border-radius: 50%;
  transition: all 0.3s;
}

.preferences-checkbox input:checked ~ span {
  background-color: #00b9ff;
}

.preferences-checkbox input:checked ~ span::before {
  left: 22px;
}

.preferences-choose__text {
  width: 100%;
  margin-top: 10px;
}

.preferences-choose__text span {
  color: #100e39;
  font-size: 10px;
  font-weight: 400;
  letter-spacing: 0.2px;
}

.preferences-choose__content {
  width: 100%;
  height: 0;
  overflow: hidden;
  transition: all 0.3s;
}

.preferences-choose__content.open {
  height: auto;
  padding-top: 15px;
}

.preferences-choose__select {
  width: 100%;
  max-width: 350px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.preferences-choose__select .dropdown-toggle {
  border: 1px solid #00b9ff;
  background-color: #ffffff;
}

.preferences-choose__select .dropdown-toggle span {
  color: rgba(0, 187, 255, 0.8);
}
.preferences-choose__select .dropdown-toggle__icons svg {
  fill: rgba(0, 187, 255, 0.8);
}
.preferences-choose__select h3 {
  color: #00b9ff;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 0.24px;
}

.preferences-team {
  margin-top: 20px;
}

.preferences-team__choose {
  width: 100%;
  max-width: 350px;
  display: flex;
  align-items: center;
  justify-content: space-between;
