}

.preferences-hours {
  width: 100%;
  max-width: 255px;
}

.preferences-hour__head {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 65px 65px;
  column-gap: 13px;
  margin-bottom: 12px;
}

.preferences-hour__head span {
