  font-size: 10px;
  font-weight: 400;
  letter-spacing: 0.2px;
  text-align: center;
}

.preferences-hours__table input.change {
  border-color: #00b9ff;
}
.preferences-hour__save {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}
.preferences-hour__save button {
  background-color: #00b9ff;
  border: none;
  color: #ffffff;
  width: 65px;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.2px;
  padding: 3px 6px;
  border-radius: 5px;
}
.preferences-hours__table span {
  color: #7f7ba0;
  font-size: 10px;
  font-weight: 400;
  letter-spacing: 0.2px;
}

.preferences-choose__title {
  margin-top: 30px;
}

.preferences-choose__title:first-child .preferences-choose:last-child {
  border-bottom: 1px solid #d1d1d1;
}

