.layout {
  width: 100%;
  height: 100%;
  display: flex;
}

.layout-container {
  width: min-content;
  flex-grow: 1;
  display: flex;
  /* height: calc(100vh - 64px); */
  position: sticky;
  top: 30px;
}

.layout-content {
  width: min-content;
  flex-grow: 1;
  height: 100%;
}

@media only screen and (max-width: 1200px) {
  .layout-content {
    width: 100%;
    flex-grow: unset;
  }
}
