.invite {
  width: 100%;
}
.invite-body {
  position: relative;
}

.invite-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  border-radius: 5px;
  border: solid 1px #cbf0ff;
  background-color: #00b9ff;
  padding: 11px 20px;
}

.invite-toggle span {
  color: #ffffff;
  font-size: 12px;
  font-weight: 500;
}

.invite-toggle svg {
  width: 12px;
  height: 12px;
  fill: #ffffff;
  margin-left: 4px;
}
.invite-toggle.open {
  border: 1px solid #cbf0ff;
  background-color: #e0f6ff;
}

.invite-toggle.open span {
  color: #00b9ff;
}

.invite-toggle.open svg {
  fill: #00b9ff;
}

.invite-content {
  position: absolute;
  z-index: 15;
  top: 45px;
  left: calc(-515px / 2);
  transform: translateX(12%);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s, visibility 0s;
}

.invite-content.open {
  visibility: visible;
  opacity: 1;
}

.invite-content__body {
  width: 515px;
  height: 250px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.09);
  background-color: #ffffff;
  position: relative;
  z-index: 1;
  border-radius: 10px;
}

.invite-content__body::after {
  content: "";
  width: 20px;
  height: 20px;
  display: block;
  transform: rotate(45deg) translateX(-50%);
  position: absolute;
  top: 0;
  left: 50%;
  background-color: #ffffff;
}

.invite-close {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding: 6px;
}
.invite-close__button {
  display: flex;
  border: none;
  background-color: transparent;
}

.invite-close__button svg {
  fill: #ff3286;
}

.invite-group {
  width: 100%;
  padding: 0 20px;
  margin-bottom: 24px;
}
.invite-group label {
  color: #100e39;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: 0.3px;
}

.invite-group__input {
  width: 100%;
  display: flex;
  border-radius: 5px;
  background-color: #f3f3f6;
  margin-top: 12px;
}
.invite-group__input input {
  width: min-content;
  flex-grow: 1;
  padding: 16px;
  border: none;
  background-color: transparent;
  color: #100e39;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.24px;
}

.invite-group__input button {
  padding: 16px;
  border: none;
  background-color: transparent;
  color: #00b9ff;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.24px;
}
