.auth {
  width: 100%;
  height: 100vh;
  background-color: #ffffff;
}

.auth-body {
  width: 100%;
  height: 100%;
  display: flex;
  padding: 30px;
}

.auth-content {
  width: 100%;
  max-width: 980px;
  margin: auto;
}

.auth-head {
  width: 100%;
}

.auth-right {
  max-width: 465px;
  max-height: 335px;
}

.auth-right img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.auth-left {
  width: 100%;
  max-width: 320px;
}

.auth-logo {
  width: 100%;
  max-width: 320px;
}
.auth-logo img {
  width: 100%;
}

.auth-center {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.auth-left__button {
  width: 100%;
  max-width: 240px;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.auth-left__button a {
  width: 100%;
  padding: 16px 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  margin-bottom: 45px;
  font-size: 21px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.19;
  letter-spacing: 0.63px;
  text-align: left;
  text-decoration: none;
}

.auth-left__button a.auth-create {
  background-color: #00b9ff;
  color: #ffffff;
}

.auth-left__button a.auth-join {
  border: solid 1px #00b9ff;
  background-color: #ffffff;
  color: #00b9ff;
}

.auth-inner {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.auth-title {
  width: 100%;
  max-width: 760px;
  text-align: center;
  margin-top: 60px;
  margin-bottom: 60px;
}

.auth-title h1 {
  font-size: 49px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
  display: block;
  text-align: center;
}

.auth-title span {
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
}

.auth-title.invite {
  margin-bottom: 20px;
}

.auth-title.invite h1 {
  font-size: 34px;
}

.auth-form {
  width: 100%;
}

.auth-form__content {
  width: 100%;
  max-width: 285px;
  margin-left: auto;
  margin-right: auto;
}

.auth-form__group {
  width: 100%;
  margin-bottom: 24px;
}
.auth-form__group button {
  width: 100%;
}
.auth-form__group button.social {
  border-radius: 10px;
  border: solid 1px #000000;
  padding: 14px;
  background-color: transparent;
  font-size: 18px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  color: #000000;
  margin-bottom: 55px;
}

.auth-or {
  width: 100%;
  height: 2px;
  background-color: #bfbfbf;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
.auth-or__content {
  width: min-content;
  padding: 10px 20px;
  background-color: #ffffff;
  font-size: 11px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.18;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
  margin-top: -13px;
}

.auth-form__group input {
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
  padding: 16px 10px;
  border-radius: 10px;
  border: solid 1px #000000;
  width: 100%;
}

.auth-form__group button.submit {
  border-radius: 5px;
  background-color: #000000;
  padding: 12px;
  font-size: 11px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.18;
  letter-spacing: normal;
  color: #ffffff;
  border: none;
}

.auth-form__group button.next {
  font-size: 26px;
}

.auth-form__group button.invite {
  font-size: 19px;
  max-width: 215px;
  margin-left: auto;
  display: block;
  margin-right: auto;
}

.auth-form__check {
  display: flex;
  justify-content: center;
  align-items: center;
}

.auth-form__check .auth-form__content {
  margin-left: 16px;
  margin-right: 16px;
}

.auth-form__check .auth-form__group {
  margin-bottom: 0;
}
.auth-form__check .auth-form__group button {
  font-size: 23px;
}

.auth-form__check .auth-form__group input {
  text-align: center;
  letter-spacing: 10px;
  font-size: 20px;
}

.auth-form__add {
  margin-bottom: 25px;
}

.auth-form__add span {
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
}

.auth-form__copy {
  width: 100%;
  display: flex;
}

.auth-form__copy button {
  width: min-content;
  border-radius: 0 10px 10px 0;
  border: solid 1px #000000;
  border-left: none;
  background-color: transparent;
  font-size: 12px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
}

.auth-form__copy input {
  border-radius: 10px 0 0 10px;
  border-right: none;
}

.auth-form__content.share {
  margin-top: 40px;
}

.auth-form__skip {
  width: 100%;
  display: block;
  text-align: center;
  font-size: 11px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.18;
  letter-spacing: normal;
  color: #000000;
  margin-top: 25px;
  text-decoration: none;
}
