.page {
  height: 100%;
}
.page-body {
  width: 100%;
  max-width: 100%;
  padding-left: 50px;
  padding-right: 50px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
}

.page-sticky {
  position: sticky;
  top: 64px;
  background-color: #fafafa;
  z-index: 100;
  padding-top: 20px;
}

.page-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 25px;
  border-bottom: 1px solid #eeeeee;
}

.page-content__title {
  color: #000000;
  font-size: 30px;
  font-weight: 700;
}

.page-content__search {
  border-radius: 17px;
  border: 1px solid #eeeeee;
  display: flex;
  align-items: center;
  padding: 11px 20px;
}

.page-content__search input {
  border: none;
  background-color: transparent;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.24px;
}
.page-content__search input::placeholder {
  color: #bfbfbf;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.24px;
}
.page-content__search button {
  background-color: transparent;
  border: none;
}

.page-content__search button svg {
  fill: #bfbfbf;
  width: 12px;
  height: 12px;
}

.page-tools {
  width: 100%;
  display: flex;
  align-items: center;
  margin-top: 25px;
  margin-bottom: 40px;
}

.page-tools__left {
  width: min-content;
  flex-grow: 1;
}

.page-choose {
  border-radius: 5px;
  background-color: #ffffff;
  padding: 6px;
}

.page-choose__list {
  display: flex;
  align-items: center;
}

.page-choose__item {
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 5px;
}

.page-choose__item:last-child {
  margin-left: 20px;
}

.page-choose__item svg {
  fill: #555464;
}

.page-choose__item.active {
  background-color: #c1eeff;
}

.page-choose__item.active svg {
  fill: #00b9ff;
}

.page-filter__body {
  display: flex;
  align-items: center;
}

.page-filter__select {
  width: 150px;
  margin-left: 42px;
}

.page-filter__select:first-child {
  margin-left: 0;
}

.page-filter__select .dropdown {
  width: 100%;
}

.page-filter__select .dropdown-toggle {
  background-color: #ffffff;
  border-radius: 5px;
  padding-top: 11px;
  padding-bottom: 11px;
  width: 100%;
}

.page-filter__select .dropdown-select {
  top: 44px;
}
.page-filter__create a,
.page-filter__create button {
  width: 100%;
  border: solid 1px #cbf0ff;
  background-color: #00b9ff;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 11px;
  color: #ffffff;
  font-size: 13px;
  font-weight: 500;
  text-decoration: none;
  border-radius: 4px;
}

.page-filter__create a svg,
.page-filter__create button svg {
  width: 15px;
  height: 15px;
  fill: #ffffff;
  margin-left: 20px;
}

.page-filter__create button:focus {
  outline: none;
}

.page-teammates {
  width: 100%;
  height: 100%;
}
.page-teammates__body {
  /* display: flex;
  flex-wrap: wrap;
  margin-left: -25px;
  margin-right: -25px; */
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 80px;
  column-gap: 55px;
  padding-bottom: 150px;
}

.page-teammates__body.list {
  grid-template-columns: 1fr;
  row-gap: 15px;
  column-gap: 0;
}

.page-tools__delete {
  border: solid 1px #cbf0ff;
  background-color: #ffd8d8;
  font-size: 10px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: center;
  color: #f08686;
  padding: 11px;
  border: none;
}

.page-inner {
  width: 100%;
  height: min-content;
  flex-grow: 1;
}

@media only screen and (max-width: 1024px) {
  /* .page-body {
    max-width: 900px;
  } */
  /* .page-sticky {
    padding-left: 30px;
    padding-right: 30px;
  }
  .page-inner {
    padding-left: 30px;
    padding-right: 30px;
  } */
  .page-teammates__bod {
    margin-left: -15px;
    margin-right: -15px;
  }
}

@media only screen and (max-width: 992px) {
  .page-filter__select {
    margin-left: 15px;
    width: 115px;
  }
  .page-body {
    padding-left: 30px;
    padding-right: 30px;
  }
  .page-teammates__body {
    column-gap: 25px;
  }
  /* .page-teammates__body {
    margin-left: -10px;
    margin-right: -10px;
  } */
}

@media only screen and (max-width: 875px) {
  .page-body {
    max-width: 100%;
  }
}
