.notification-item {
  width: 100%;
  padding: 0 35px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  cursor: pointer;
  display: grid;
  grid-template-columns: 60px 0.8fr 1fr 1.2fr 1fr 25px;
  white-space: nowrap;
}

.notification-item__image {
  height: 60px;
  border: solid 1px #707070;
  border-radius: 50%;
}
.notification-item__image img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.notification-item__name {
  /* width: min-content;
  flex-grow: 1; */
  padding-left: 20px;
}

.notification-item__text {
  font-size: 21px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.19;
  letter-spacing: normal;
  text-align: center;
  color: #042141;
}

.notification-item__name {
  text-align: left;
}

.notification-item__delete button {
  width: 24px;
  height: 24px;
  cursor: pointer;
  padding: 0;
  background-color: transparent;
  border: none;
}

.notification-item__delete svg {
  fill: #f08686;
  width: 100%;
  height: 100%;
}

.notification-item__time {
  /* width: 130px; */
  text-align: center;
  border-left: 2px solid #bfbfbf;
}

.notification-item__time span {
  display: block;
}

.notification-item__type {
  /* width: 300px; */
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 2px solid #bfbfbf;
}

.notification-item__type svg {
  width: 22px;
  height: 22px;
  fill: #f08686;
}

.notification-item__type span {
  margin-left: 10px;
}

.notification-item__status {
  /* width: 200px; */
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 2px solid #bfbfbf;
}
.notification-item__status span {
  margin-left: 10px;
}

.notification-item__status svg {
  width: 12px;
  height: 12px;
  fill: #f08686;
}

.notification-item__status svg.online {
  fill: #6ac000;
}

.notification-item__status svg.meeting {
  fill: #0a8aff;
}

@media only screen and (max-width: 1200px) {
  .notification-item__text span {
    font-size: 16px;
  }
}

@media only screen and (max-width: 1024px) {
  .notification-item {
    grid-template-columns: 30px 0.8fr 1fr 1.2fr 1fr 25px;
  }
  /* .notification-item__type {
    width: 200px;
  }
  .notification-item__status {
    width: 160px;
  } */
  /* .notification-item__text span {
    font-size: 16px;
  } */
  .notification-item__image {
    /* width: 30px; */
    height: 30px;
  }
}
