.dropdown {
  display: inline-block;
}

.dropdown-body {
  position: relative;
}

.dropdown-toggle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 5px;
  border: solid 1px #eeeeee;
  background-color: transparent;
  padding: 6px;
  width: 200px;
}
.dropdown-toggle.open {
  border-color: #00b9ff;
}

.dropdown-toggle span {
  color: #000000;
  font-size: 13px;
  font-weight: 500;
  width: min-content;
  flex-grow: 1;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  text-align: left;
}

.dropdown-toggle__icons {
  margin-left: 6px;
  padding-top: 3px;
}

.dropdown-toggle__icons svg {
  display: block;
  width: 7px;
  height: 5px;
  fill: #555464;
}

.dropdown-toggle__icons svg:first-child {
  margin-bottom: 2px;
}

.dropdown-select {
  min-width: 100%;
  position: absolute;
  top: 33px;
  left: 0;
  z-index: 10;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08);
  border-radius: 5px;
  border: 1px solid #eeeeee;
  background-color: #ffffff;
  padding: 5px 0;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s, visibility 0s;
}

.dropdown-select.open {
  visibility: visible;
  opacity: 1;
}

.dropdown-item {
  color: #000000;
  font-size: 13px;
  font-weight: 400;
  width: 100%;
  margin-bottom: 4px;
  padding: 2px 5px;
  transition: all 250ms;
  cursor: pointer;
  white-space: nowrap;
}

.dropdown-item:hover {
  background-color: #e0f6ff;
  color: #00b9ff;
}
