.coming {
    width: 100%;
}

.coming-body {
    width: 100%;
    max-width: 845px;
    margin-left: auto;
    margin-right: auto;
   padding: 30px;
}

.coming-body__head {
    width: 100%;
    text-align: center;
    margin-bottom: 45px;
}

.coming-body__head h1 {
    font-size: 30px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: normal;
    color: #000000;
    margin-bottom: 25px;
}
.coming-body__head p {
    font-size: 20px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: normal;
    color: #000000;
}

.coming-body__image {
    width: 100%;
}

.coming-body__image img {
    width: 100%;
}