.crmeeting {
  width: 100%;
}
.crmeeting-body {
  width: 100%;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  margin-bottom: 50px;
}

.crmeeting-head {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

.crmeeting-head h1 {
  color: #253655;
  font-size: 22px;
  font-weight: 500;
}

.crmeeting-form {
  width: 100%;
}

.crmeeting-form__body {
  width: 100%;
}

.crmeeting-form__group {
  width: 100%;
  margin-bottom: 30px;
}

.crmeeting-form__group label {
  color: #253655;
  font-size: 22px;
  font-weight: 500;
  padding-left: 15px;
  margin-bottom: 16px;
  display: block;
}

.crmeeting-form__title {
  width: 100%;
  display: flex;
  border-radius: 12px;
  border: 2px solid #00b9ff;
  position: relative;
}

.crmeeting-form__title input {
  width: min-content;
  flex-grow: 1;
  padding: 22px;
  background-color: transparent;
  border: none;
  color: #253655;
  font-size: 14px;
  font-weight: 400;
}

.crmeeting-form__emoji {
  padding-left: 24px;
  border: none;
  background-color: transparent;
}

.crmeeting-form__emoji svg {
  fill: #253655;
  width: 19px;
  height: 19px;
}

.crmeeting-date {
  width: 100%;
}

.crmeeting-date__body {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.crmeeting-date__container {
  display: grid;
  grid-template-columns: 155px 110px;
  column-gap: 20px;
}

.crmeeting-date__input {
  position: relative;
  border-radius: 11px;
  border: 2px solid #00b9ff;
}

.crmeeting-date__input input {
  width: 100%;
  border: none;
  background-color: transparent;
  padding: 11px;
  color: #253655;
  font-size: 14px;
  font-weight: 400;
}

.crmeeting-date__input svg {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  fill: rgba(37, 54, 85, 0.9);
}

.crmeeting-date__to {
  color: #253655;
  font-size: 14px;
  font-weight: 400;
}

.crmedia {
  width: 100%;
  padding-left: 15px;
}
.crmedia-body {
  width: 100%;
  display: grid;
  grid-template-columns: 160px 160px;
  column-gap: 50px;
}

.crmedia-container__label {
  color: #253655;
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 14px;
  display: block;
}

.crmedia-container__content {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 12px;
}

.crmedia-container__type {
  color: #253655;
  font-size: 18px;
  font-weight: 400;
}

.crmedia-container__checkbox {
  width: 29px;
  height: 15px;
  cursor: pointer;
}
.crmedia-container__checkbox span {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
  border-radius: 8px;
  background-color: #dcdde4;
  transition: all 0.3s;
}

.crmedia-container__checkbox span::before {
  content: "";
  width: 11px;
  height: 11px;
  background-color: #515778;
  border-radius: 50%;
  position: absolute;
  left: 2px;
  top: 2px;
  transition: all 0.3s;
}
.crmedia-container__checkbox input:checked ~ span {
  background-color: #00b9ff;
}

.crmedia-container__checkbox input:checked ~ span::before {
  background-color: #ffffff;
  left: 16px;
}

.crinvite {
  width: 100%;
}

.crinvite-body {
  width: 100%;
  max-width: 250px;
  margin-top: 60px;
}

.crinvite-body__input {
  border-radius: 11px;
  border: 2px solid #00b9ff;
  padding: 11px 16px;
  color: #253655;
  font-size: 14px;
  font-weight: 400;
  width: 100%;
}
.crinvite-persons {
  width: 100%;
}
.crinvite-persons__item {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.crinvite-persons__item .image {
  width: 30px;
  height: 30px;
  border: 1px solid #707070;
  border-radius: 50%;
}

.crinvite-persons__item .name {
  width: min-content;
  flex-grow: 1;
  padding: 0 6px;
  color: #253655;
  font-size: 9px;
  font-weight: 400;
}

.crinvite-persons__item .invite {
  border-radius: 6px;
  background-color: #3fcaff;
  color: #ffffff;
  font-size: 10px;
  font-weight: 400;
  padding: 3px 16px;
  width: min-content;
  margin-left: 14px;
  margin-right: 14px;
}

.crinvite-persons__item .delete {
  padding: 0;
  background-color: transparent;
  border: none;
  margin-left: 14px;
}

.crinvite-persons__item .delete svg {
  width: 9px;
  height: 9px;
  fill: #253655;
}

.crcalendar {
  width: 100%;
  margin-bottom: 50px;
}
.crcalendar-body {
  width: 100%;
  max-width: 250px;
  margin-top: 45px;
}

.crcalendar-body label {
  color: #253655;
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 20px;
  padding-left: 15px;
  display: block;
}

.crcalendar-dropdow {
  width: 100%;
  position: relative;
}

.crcalendar-toggle {
  width: 100%;
  border-radius: 11px;
  border: 2px solid #00b9ff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: transparent;
  padding: 11px 16px;
}

.crcalendar-content {
  position: absolute;
  top: 50px;
  left: 0;
  width: 100%;
  z-index: 10;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08);
  border-radius: 5px;
  border: 1px solid #eeeeee;
  background-color: #ffffff;
  padding: 5px 0;
  transition: opacity 0.3s, visibility 0s;
  border: 2px solid #00b9ff;
  visibility: hidden;
  opacity: 0;
}

.crcalendar-content.open {
  visibility: visible;
  opacity: 1;
}

.crcalendar-item {
  width: 100%;
  padding: 6px 16px;
  cursor: pointer;
  transition: all 0.2s;
}

.crcalendar-item:hover {
  background-color: #e0f6ff;
  color: #00b9ff;
}

.crmeeting-form__submit {
  width: 100%;
  margin-top: 20px;
}

.crmeeting-form__submit button {
  width: 100%;
  border-radius: 11px;
  background-color: #00b9ff;
  color: #ffffff;
  font-size: 30px;
  font-weight: 400;
  border: none;
  padding: 12px 16px;
}

.meeting {
  width: 100%;
}

.meeting-body {
  width: 100%;
}
.meeting-container {
  width: 100%;
  max-width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 30px;
  row-gap: 55px;
}

.meeting-content {
  box-shadow: 0 6px 3px rgba(0, 0, 0, 0.02);
  border-radius: 8px;
  background-color: #ffffff;
  padding: 16px;
  min-height: 210px;
  display: flex;
  flex-direction: column;
}

.meeting-content__head {
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: auto;
}

.meeting-content__head h2 {
  color: #555464;
  font-size: 20px;
  font-weight: 500;
  width: min-content;
  flex-grow: 1;
  padding-left: 20px;
  padding-right: 20px;
}

.meeting-content__head span {
  margin-left: auto;
}

.meeting-content__head button {
  /* padding: 0; */
  background-color: #ffffff;
  border: none;
}

.meeting-content__head button svg {
  width: 15px;
  height: 13px;
  fill: #707070;
}

.meeting-tools {
  width: min-content;
  white-space: nowrap;
  position: absolute;
  top: -5px;
  left: 108%;
  z-index: 100;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08);
  background-color: #ffffff;
}

/* .meeting-tools */
.meeting-tools__body {
  width: 100%;
  height: 100%;
  position: relative;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08);
  background-color: #ffffff;
}
.meeting-tools__body::before {
  content: "";
  width: 20px;
  height: 20px;
  /* box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08); */
  background-color: #ffffff;
  display: block;
  position: absolute;
  left: -10px;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
}

.meeting-tools__content {
  padding: 16px;
  padding-right: 35px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
}

.meeting-tools__content p {
  color: #656565;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 0.2px;
  margin-bottom: 16px;
}

.meeting-tools__content p:last-child {
  margin-bottom: 0;
}
.meeting-tools__content p a {
  color: #656565;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 0.2px;
  text-decoration: none;
}

.meeting-content__center {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: auto;
  margin-bottom: auto;
}

.meeting-content__tools {
  display: flex;
  align-items: center;
}

.meeting-content__tools span {
  border-radius: 2px;
  background-color: #eeeeee;
  color: #555464;
  font-size: 20px;
  font-weight: 400;
}

.meeting-content__tools div {
  color: #eeeeee;
  margin-left: 12px;
  margin-right: 12px;
}

.meeting-content__tools img {
  width: 24px;
  height: 24px;
  object-fit: contain;
}

.meeting-content__center button {
  padding: 0;
  background-color: transparent;
  border: none;
}

.meeting-content__center button svg {
  fill: #555464;
}

.meeting-content__bottom {
  width: 100%;
 margin-top: 45px;
  position: relative;
}

.meeting-content__title {
  font-size: 10px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: left;
  color: #707070;
  margin-bottom: 8px;
}

.meeting-content__images {
  display: grid;
 grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
 column-gap: 10px ;
}

.meeting-content__images img {
  border-radius: 7px;
  width: 100%;
  object-fit: cover;
}

.meeting-content__more {
 
  background-color: #f3f8fd;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;
}
.meeting-content__more span {
  color: #00b9ff;
  font-size: 9px;
  font-weight: 500;
  display: block;
  text-align: center;
}

.meeting-content__plus {
  width: 34px;
  height: 34px;
  border: 1px dashed #00b9ff;
  background-color: #c1eeff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.meeting-content__plus svg {
  width: 12px;
  height: 12px;
  fill: #00b9ff;
}

.meeting-search {
  width: 100%;
  max-width: 250px;
  border-radius: 5px;
  border: 1px solid #707070;
  background-color: #ffffff;
  position: absolute;
  top: 40px;
  right: -16px;
  z-index: 110;
}

.meeting-search__body {
  width: 100%;
  padding: 16px;
}

.meeting-search__head {
  display: flex;
  border-radius: 5px;
  border: 1px solid #dddddd;
  background-color: #ffffff;
}

.meeting-search__head input {
  border: none;
  background-color: transparent;
  height: 30px;
  width: min-content;
  flex-grow: 1;
}

.meeting-search__head button {
  border: none;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}

.meeting-search__head button svg {
  fill: #dddddd;
}

.meeting-search__list {
  width: 100%;
  margin-top: 30px;
  max-height: 210px;
  overflow: scroll;
}
.meeting-search__list::-webkit-scrollbar {
  display: none;
}

.meeting-search__item {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.meeting-search__item img {
  width: 34px;
  height: 34px;
  border: 1px solid #707070;
  border-radius: 50%;
}

.meeting-search__item span {
  width: min-content;
  flex-grow: 1;
  color: #152243;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.14px;
  line-height: 18px;
  padding-left: 20px;
  padding-right: 10px;
}

.meeting-search__item button {
  border-radius: 5px;
  border: 1px solid #00b9ff;
  background-color: #ffffff;
  padding: 3px 12px;
  color: #00b9ff;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.14px;
  line-height: 18px;
}


.meeting-content__emojy {
  width: 68px;
  height: 68px;
  border-radius: 8px;
  background-color: rgba(216, 232, 248, 0.3);
  display: flex;
}

.meeting-content__emojy  span {
  font-size: 20px;
  margin: auto;
  text-align: center;
  color: #0089ff;
}

.meeting-settings {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 20px;
  margin-top: 40px;
  position: relative;
}
.meeting-settings button { 
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.04);
  background-color: #ffffff;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 13px;
  white-space: nowrap;
}

.meeting-settings button span {
  margin-left: 10px;
}

.meeting-settings button span {
  font-size: 8px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  letter-spacing: normal;
  text-align: left;
  color: #707070;
}

.meeting-settings button svg {
  width: 15px;
  height: 10px;
  fill: #707070;
}

.meeting-settings .meeting-settings__tool svg {
  fill: #0089ff;
}

.meeting-join {
  width: 100%;
  margin-top: 15px;
  display: flex;
  justify-content: center;
}

.meeting-join button {
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.04);
  background-color: #ffffff;
  padding: 14px;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  text-align: left;
  color: #0089ff;
  border: none;
}

.meeting-join button:focus {
  outline: 0;
}

.meeting-content__center {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: auto;
  margin-bottom: auto;
  height: 0;
  overflow: hidden;
  transition: all 0.3s;
}
.meeting-content__center.show {
  height: auto;
}


.meeting-persons {
  width: 100%;
  position: relative;
}
.meeting-persons__hide {
  border-radius: 2px;
  background-color: #c1eeff;
  display: inline-block;
  padding: 6px;
  line-height: 0;
  position: absolute;
  top: 0px;
  right: 6px;
  cursor: pointer;
}
.meeting-persons__hide svg {
  width: 7.7px;
  height: 5.2px;
  fill: #00b9ff;
}
.meeting-persons__body {
  width: 100%;
}

.meeting-persons__head {
  display: flex;
  border-radius: 5px;
}

.meeting-persons__head input {
  border: none;
  background-color: transparent;
  height: 30px;
  width: min-content;
  flex-grow: 1;
}

.meeting-persons__head button {
  border: none;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}

.meeting-persons__head button svg {
  fill: #dddddd;
}

.meeting-persons__list {
  width: 100%;
  margin-top: 30px;
}
.meeting-persons__list::-webkit-scrollbar {
  display: none;
}

.meeting-persons__item {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.meeting-persons__item img {
  width: 34px;
  height: 34px;
  border: 1px solid #707070;
  border-radius: 50%;
}

.meeting-persons__item span {
  width: min-content;
  flex-grow: 1;
  color: #152243;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.14px;
  line-height: 18px;
  padding-left: 20px;
  padding-right: 10px;
}

.meeting-persons__item button {
  border-radius: 5px;
  border: 1px solid #00b9ff;
  background-color: #ffffff;
  padding: 3px 12px;
  color: #00b9ff;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.14px;
  line-height: 18px;
}


@media only screen and (max-width: 1366px) {
  .meeting-settings {
    grid-template-columns: 1fr 1fr;
    row-gap: 12px;
  }
}

@media only screen and (max-width: 1200px) {
  .meeting-container {
    grid-template-columns: 1fr 1fr;
  }
}
