.teammates {
  height: auto;
  cursor: pointer;
}
.teammates.list {
  width: 100%;
}

.teammates-body {
  width: 100%;
  height: 100%;
  position: relative;
}

.teammates-body.list {
  background-color: transparent;
  box-shadow: unset;
}

.teammates-body__grid {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.04);
  background-color: #ffffff;
  border-radius: 10px;
}

.teammates-body.list .teammates-body__grid {
  display: none;
}
.teammates-body__list {
  display: none;
  /* padding: 14px 0; */
}
.teammates-body.list .teammates-body__list {
  display: block;
}

.teammates-about {
  width: 100%;
  height: 100%;
  padding: 13px;
  display: flex;
}

.teammates-about__image {
  width: 60px;
  height: 60px;
}

.teammates-about__image--name {
  background: #7e7ba0;
  border-radius: 50%;
  justify-content: center;
  display: flex;
  align-items: center;
}

.teammates-about__image--name p {
  color: #fff;
  font-size: 20px;
}

.teammates-about__image img {
  width: 100%;
  height: 100%;
  border-radius: 8px;
}

.teammates-about__data {
  width: min-content;
  flex-grow: 1;
  padding-left: 16px;
}

.teammates-about__name {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 18px;
}

.teammates-about__name h2 {
  color: #555464;
  font-size: 20px;
  font-weight: 400;
}

.teammates-about__name img {
  margin-left: 12px;
}

.teammates-about__item {
  width: 100%;
  margin-bottom: 12px;
}

.teammates-about__item svg {
  width: 11px;
  height: 11px;
  fill: #b3b2b4;
}

.teammates-about__item svg.icon-circul {
  fill: #f08686;
}

.teammates-about__item svg.meeting {
  fill: #0a8aff;
}

.teammates-about__item svg.online {
  fill: #6ac000;
}

.teammates-about__item span {
  color: #b3b2b4;
  font-size: 14px;
  font-weight: 400;
  margin-left: 10px;
}

.teammates-body__content {
  position: absolute;
  top: 80px;
  left: 0;
  z-index: 10;
  width: 100%;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.04);
  background-color: #ffffff;
  border-radius: 0 0 10px 10px;
}

.teammates-tools {
  padding: 15px;
  padding-bottom: 0;
}

.teammates-tools__item {
  display: flex;
  align-items: center;
  margin-bottom: 4px;
  padding: 5px;
  width: min-content;
  max-width: 100%;
  overflow: hidden;
}

.teammates-tools__item svg {
  width: 14px;
  height: 14px;
  fill: #6f6e7c;
}

.teammates-tools__item img {
  height: 16px;
}

.teammates-tools__item span {
  color: #52515b;
  font-size: 12px;
  font-weight: 400;
  margin-left: 10px;
  white-space: nowrap;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.teammates-tools__item a {
  color: #52515b;
  font-size: 8px;
  font-weight: 400;
  text-decoration: none;
}
.teammates-tools__item:hover {
  border-radius: 5px;
  background-color: #c1eeff;
}

.teammates-tools__item:hover a {
  color: #00b9ff;
  text-decoration: none;
}

.teammates-tools__item:hover svg {
  fill: #00b9ff;
}
.teammates-tools__item:hover span {
  color: #00b9ff;
}

.teammates-tools__write {
  width: 100%;

  /* background-color: #dfdee2; */
  border-radius: 0 0 10px 10px;
  padding: 0 20px;
  margin-bottom: 16px;
}
.teammates-tools__write input {
  width: 100%;
  border-radius: 24px;
  border: solid 1px #eeeeee !important;
  color: #52515b;
  font-size: 12px;
  font-weight: 400;
  border: none;
  padding: 16px 10px;
  display: block;
}

.teammates-chat {
  position: absolute;
  top: -150px;
  right: -340px;
  /* transform: translateY(-50%); */
  z-index: 110;
  width: 350px;
  height: 400px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.04);
  border: 1px solid #e2dfdf;
  background-color: #ffffff;
  border-radius: 10px;
  display: none;
}

.teammates-chat.drop .teammates-chat__body:before {
  display: none;
}

.teammates-chat.open {
  display: block;
}

.teammates-chat.right {
  left: -340px;
}

.teammates-chat.right .teammates-chat__body:before {
  left: unset;
  right: -32px;
}

.teammates-chat.top {
  left: 0;
  top: unset;
  bottom: 110px;
}

.teammates-chat.top .teammates-chat__body:before {
  left: unset;
  right: 50%;
  bottom: -17px;
  top: unset;
  transform: translateX(50%) rotate(45deg);
}

.teammates-chat__body {
  position: relative;
  width: 100%;
  height: 100%;
}

.teammates-chat__body:before {
  content: "";
  position: absolute;
  top: 45%;
  left: 0px;
  transform: translateX(-50%) rotate(-45deg);
  width: 30px;
  height: 30px;
  background-color: #ffffff;
  display: block;
  border: 1px solid #e2dfdf;
}

.teammates-chat__content {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}

.teammates-chat__head {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding: 10px;
  margin-bottom: auto;
}

.teammates-chat__type {
  display: flex;
  background-color: transparent;
  border: none;
}
.teammates-chat__type svg {
  width: 20px;
  height: 20px;
  fill: #00b9ff;
}

.teammates-chat__type.close svg {
  fill: #ff3286;
}

.teammates-chat__write {
  margin-top: auto;
}

.teammates-chat__messages {
  width: 100%;
  height: min-content;
  flex-grow: 1;
  padding: 10px;
}

.teammates-chat__text {
  width: 100%;
  margin-bottom: 10px;
  display: flex;
}

.teammates-chat__text span {
  min-width: max-content;
  max-width: 100%;
  border-radius: 20px;
  border: solid 1px #eeeeee;
  font-size: 14px;
  font-weight: 500;
  padding: 12px 24px;
}

.teammates-chat__text.right span {
  min-width: max-content;
  max-width: 100%;
  border-radius: 20px;
  background-color: #f8f8f8;
  color: #042141;
  font-size: 14px;
  font-weight: 500;
  padding: 12px 24px;
  margin-left: auto;
}

/* .teammates-chat__text.left span {
  background-color: #f4f4f4;
} */

.teammates-list {
  width: 100%;
  /* display: flex;
  align-items: center; */
  display: grid;
  grid-template-columns: 30px 1fr 1fr 1fr 1fr 50px 50px 50px;
}

.teammates-list__image {
  /* width: 30px; */
  height: 30px;
  border: solid 1px #707070;
  border-radius: 50%;
}

.teammates-list__image img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.teammates-list__name {
  /* width: min-content;
  flex-grow: 1; */
  padding-left: 25px;
}
.teammates-list__name span {
  font-size: 15px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  color: #042141;
}

.teammates-list__button {
  display: flex;
  justify-content: center;
  /* width: 50px; */
  background-color: transparent;
  border: none;
}

.teammates-list__button svg {
  fill: #6f6e7c;
}
.teammates-list__button.chat svg {
  fill: #00b9ff;
}
/* .teammates-list__document {
  width: 160px;
} */

.teammates-list__document a {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.teammates-list__document a span {
  width: min-content;
  flex-grow: 1;
  font-size: 15px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: center;
  color: #bab9bb;
  padding-left: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.teammates-list__document a img {
  width: 1rem;
  height: 1rem;
  object-fit: contain;
}

.teammates-list__time {
  display: flex;
  align-items: center;
  /* width: 200px; */
}

.teammates-list__time span {
  font-size: 15px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: center;
  color: #042141;
  margin-left: 5px;
}

.teammates-list__time svg {
  fill: #042141;
}

.teammates-list__status {
  /* width: 200px; */
  display: flex;
  align-items: center;
}

.teammates-list__status svg.icon-circul {
  width: 9px;
  height: 9px;
  margin-right: 5px;
}

.teammates-list__status svg.icon-circul {
  fill: #f08686;
}

.teammates-list__status svg.meeting {
  fill: #0a8aff;
}

.teammates-list__status svg.online {
  fill: #6ac000;
}

.teammates-body__list .teammates-chat,
.teammates-body__list .teammates-chat.right {
  right: 60px;
  top: -190px;
}

.teammates-body__list .teammates-chat__body::before {
  left: unset;
  right: -33px;
}

.teammates-bar {
  width: 100%;
  padding: 16px;
  display: grid;
  grid-template-columns: 28px 28px 28px 1fr;
  column-gap: 20px;
}

.teammates-bar__item {
  height: 28px;
  display: flex;
  background-color: #f7f7f7;
}

.teammates-bar__icon {
  width: 100%;
  max-width: 28px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f7f7f7;
  border-radius: 5px;
  transition: all 0.25s;
}

.teammates-bar__icon.disabled{
  opacity: 0.5!important;
}

.teammates-bar__icon.phone:hover {
  background-color: #f1fcf7;
}
.teammates-bar__icon.phone:hover svg {
  fill: #3bd98a;
}

.teammates-bar__icon.video:hover {
  background-color: #ffe9ec;
}
.teammates-bar__icon.video:hover svg {
  fill: #ff9592;
}

.teammates-bar__icon.chat:hover {
  background-color: #ffddb4;
}
.teammates-bar__icon.chat:hover svg {
  fill: #ff9d2a;
}

.teammates-bar__icon svg {
  fill: #aaaaaa;
}

.teammates-bar__doc {
  width: min-content;
  flex: 1;
  background-color: #f7f7f7;
  border-radius: 5px;
  display: flex;
  align-items: center;
}

.teammates-bar__doc span {
  font-size: 8px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  letter-spacing: normal;
  text-align: left;
  color: #aaaaaa;
}

.teammates-bar__item.doc:hover .teammates-bar__icon svg {
  fill: #00a0f1;
}

.teammates-bar__item.doc:hover .teammates-bar__doc span {
  color: #676770;
}
