.status {
  margin-left: 40px;
}

.status-body {
  position: relative;
}

.status-toggle {
  width: 31px;
  height: 31px;
  position: relative;
  cursor: pointer;
}

.status-toggle img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

.status-toggle:after {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #4bcc7d;
  border: 2px solid white;
  display: block;
  border-radius: 50%;
  position: absolute;
  top: -8px;
  right: -10px;
}

.status-content {
  position: absolute;
  right: -35px;
  top: 55px;
  z-index: 10;
  min-width: 335px;
  white-space: nowrap;
  border-radius: 20px;
  box-shadow: 0 2px 90px rgba(0, 0, 0, 0.16);
  background-color: #ffffff;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s, visibility 0s;
}

.status-content.open {
  visibility: visible;
  opacity: 1;
}

.status-content__body {
  position: relative;
  padding: 24px 0;
}

.status-content__body:after {
  content: "";
  width: 25px;
  height: 25px;
  display: block;
  background-color: #ffffff;
  position: absolute;
  top: -10px;
  right: 35px;
  transform: rotate(45deg);
}

.status-content__head {
  width: 100%;
  padding: 0 24px;
  padding-bottom: 10px;
  border-bottom: 2px solid #e6e6e6;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.status-content__head svg, svg.active {
  fill: #4bcc7d;
}

.status-content__head span {
  color: #2b325b;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.75px;
  margin-left: 20px;
}

.status-custom__head {
  padding: 0 14px;
}
.status-custom__head span {
  color: #a39aa3;
  font-size: 9px;
  font-weight: 400;
  letter-spacing: 0.45px;
}

.status-custom__center {
  margin-top: 20px;
  /* padding-left: 20px;
  padding-right: 10px; */
}

.status-item {
  width: 100%;
  margin-bottom: 5px;
  cursor: pointer;
}
.status-item__content {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}
.status-item__icon {
  width: 32px;
  height: 32px;
}

.status-item__name {
  border-radius: 5px;
  color: #2b325b;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.75px;
  display: block;
  padding: 11px 9px;
  margin-left: 5px;
  margin-right: 5px;
}

.status-item__name.bg-color {
  background-color: #d9eff8;
}

.status-item__about {
  width: 100%;
  display: flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 10px;
  transition: all 0.2s;
}

.status-edit {
  display: flex;
  margin-left: auto;
}

.status-edit input {
  width: 70px;
  border-radius: 5px;
  background-color: #f9f8f9;
  padding: 11px 9px;
  border: none;
  text-align: center;
  color: #a39aa3;
  font-size: 12px;
  font-weight: 400;
  margin-left: 5px;
  margin-right: 5px;
}

.status-edit button {
  width: 65px;
  border-radius: 5px;
  background-color: #d9eff8;
  padding: 11px 9px;
  border: none;
  margin-left: 5px;
  margin-right: 5px;
  color: #00b1f4;
  font-size: 12px;
  font-weight: 400;
}
.status-edit input:focus,
.status-edit button:focus {
  outline: 0;
}

.status-item__text {
  width: 100%;
  max-width: 225px;
  display: block;
  color: #2b325b;
  font-size: 10px;
  font-weight: 400;
  letter-spacing: 0.2px;
  white-space: normal;
  margin-bottom: 12px;
  padding-left: 25px;
}

.status-custom__bottom {
  padding: 0 20px;
}

.status-write__body {
  border-radius: 15px;
  background-color: #f9f8f9;
  display: flex;
  align-items: center;
  padding: 13px;
  position: relative;
}

.status-write__emoji {
  background-color: transparent;
  border: none;
}

.status-write__emoji svg {
  width: 19px;
  height: 19px;
  fill: #a39aa3;
}

.status-write__text {
  width: min-content;
  flex-grow: 1;
  background-color: transparent;
  border: none;
  border-left: 2px solid #a39aa3;
  padding-left: 10px;
  color: #a39aa3;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.75px;
}

.status-write__edit {
  justify-content: space-between;
  margin-top: 14px;
}

.status-write__edit input,
.status-write__edit button {
  margin-left: 0;
  margin-right: 0;
}
/* #emoji {
  position: absolute;
  top: -250px;
  left: 2px;
} */
.status-item__delete {
  margin-left: auto;
  opacity: 0;
  transition: 0.2s;
}

.status-item__about.close:hover {
  background-color: #d9eff8;
}

.status-item__about:hover .status-item__delete {
  opacity: 1;
}

.status-item__delete svg {
  fill: #f08686;
}

.logout-btn{
      width: 100%;
    display: flex;
    justify-content: center;
    padding: 20px 25px;
}

.logout-btn > button{
      width: 100%;
    background: #0b8aff;
    color: #fff;
    border: none;
    outline: none;
    padding: 8px 0px;
    border-radius: 4px;
}
