.navbar {
  width: 100%;
  height: min-content;
  flex-grow: 1;
}
.navbar-body {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.navbar-body a {
  margin-bottom: 15px;
  margin-top: 15px;

  padding: 6px;
  border-radius: 6px;
}

.navbar-body a svg {
  width: 25px;
  height: 20px;
  fill: #ffffff;
}

.navbar-body a:hover,
.navbar-body a:active,
.navbar-body a.active {
  background-color: rgba(255, 255, 255, 0.5);
}

.navbar-body a:hover svg,
.navbar-body a:active svg,
.navbar-body a.active svg {
  border-radius: 8px;
}

@media only screen and (max-width: 1200px) {
  .navbar-body {
    flex-direction: unset;
    padding-left: 10px;
    padding-right: 10px;
  }
  .navbar-body a {
    width: calc(100% / 5);
    text-align: center;
    margin-left: 10px;
    margin-right: 10px;
  }
}
