.header {
  width: 100%;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  background-color: #ffffff;
  z-index: 110;
  position: sticky;
  top: 0;
}

.header-body {
  width: 100%;
  height: 64px;
  display: flex;
  align-items: center;
}

.header-logo {
  width: 120px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-logo__link {
  display: block;
  width: 41px;
  height: 45px;
}

.header-logo__link img {
  width: 100%;
  height: 100%;
}

.header-content {
  width: min-content;
  flex-grow: 1;
  height: 100%;
}

.header-content__body {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 50px;
  padding-right: 50px;
}

.header-content__aside {
  display: flex;
  align-items: center;
}
