.aside {
  width: 120px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.06);
  background-color: #00b9ff;
  position: sticky;
  top: 0;
  z-index: 1;
  height: 100vh;
  flex-shrink: 0;
}

.aside-body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.aside-logo {
  width: 100%;
  height: 64px;
}

.aside-logo a {
  width: 100%;
  height: 100%;
  display: flex;
}

.aside-logo a img {
  margin: auto;
  object-fit: contain;
}

.aside-bottom {
  width: 100%;
}
.aside-bottom a {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 20px 6px;
  border-radius: 6px;
}

.aside-bottom a svg {
  fill: #ffffff;
  width: 20px;
  height: 20px;
}
.aside-bottom a:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

@media only screen and (max-width: 1200px) {
  .aside {
    width: 100vw;
    height: auto;
    position: fixed;
    z-index: 100;
    bottom: 0;
    top: unset;
  }
  .aside-body {
    flex-direction: unset;
  }

  .aside-logo {
    width: 120px;
  }
  .aside-bottom {
    width: 100px;
    display: flex;
  }
  .aside-bottom a {
    margin: auto;
    padding: 6px;
    margin-right: 10px;
  }
}
